.mySwiper2 {
    width: 1800px;
    padding: 10px 80px;
    height: auto;
    margin-top: 30px;
    position: relative;
}

.mySwiper2 .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    width: 293px;
    margin-right: 40px;
    border-radius: 15px;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
}

.custom-slide {
    width: 293px;
}

.mySwiper2 .swiper-scrollbar {
    width: 100%;
    height: 5px;
    margin-top: 40px;
    background: rgba(0, 0, 0, .1);
    border-radius: 3px;
}

.mySwiper2 .swiper-scrollbar .swiper-scrollbar-drag {
    width: 573.513px !important;
    background-color: rgba(0, 0, 0, 1);
    height: 5px;
}