.mySwiper1 {
    width: 1350px;
    height: auto;
    margin-top:100px;
    position:relative;
}   

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-pagination-clickable .swiper-pagination-bullet{
    cursor:none;
}

.swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    background: transparent;
    border: 1px solid #8581d5;
    opacity: 1;
}

.swiper-pagination-bullet-active {
    width: 25px;
    transition: width .5s;
    border-radius: 5px;
    background: #8581d5;
    border: 1px solid transparent;
    cursor:none;
}