.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

a {
  text-decoration:none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.start {
  transform: scale(0);

}
.end {
   transform: scale(1);
   transition :all 0.5s;
}

/* 네비 */

.top-nav {height:50px; display:flex; justify-content:flex-end; margin:15px 70px 0 0;}

.top-nav > a {font-size:13px; color:#000; font-weight:400; padding:8px 10px;}
.top-nav > a:hover {color:darkturquoise;}

.bot-nav {height:80px; position:relative;}
.main-nav {display:flex; float:left; margin:10px 0 0 65px;}
.main-nav > div {display:flex;}
.main-nav > div:first-child:after {content:''; height:19px; width:1px; background-color:#000; margin:13px 30px 0; display:inline-block;}
.main-nav > div:nth-child(2):after {content:''; height:19px; width:1px; background-color:#000; margin:13px 30px 0; display:inline-block;}

.bot-nav {background-color:#f8f8f8f8; position:sticky; top:0; z-index:10;}
.bot-nav a {float:left; color:#000; font-size:16px; line-height:26px; font-weight:500; padding:8px 10px;}
.bot-nav div a:hover {color:#8581d6;}

.bot_nav_shipping {width:260px;}

.logo {width:85px; height:40px; float:left; margin:-4px 0 0 100px;}
.logo > img {width:100%; height:100%;}

.new {position:relative;}
.new:after {position:absolute; top:0; content:"N"; font-size:12px; font-weight:bold; color:#8581d6;}

.new2 {position:relative;}
.new2:after {position:absolute; top:4px; right:36px; content:"N"; font-size:12px; font-weight:bold; color:#8581d6;}

.dropdown-toggle::after {display:none;}

.sertch {position:absolute; width:120px; margin-left:20px; top:17px; right:205px; border:none; border-bottom:1px solid #000; height:20px;}
#glass {position:absolute; font-size:13px; top:20px; right:210px;}

.Cartbtn {position:absolute; top:0px; right:140px; }
.Cartbtn img {width:28px;}

.menubtn {position:absolute; top:5px; right:90px;}
.menubtn:hover {color:#8581d6; }

/* Popup Modal */
.popup_wrap {padding:40px 0 60px; width:100%; position:absolute; top:80px; left:0; background-color: #f9f9f9;}

.popup_content {margin:0 260px; display:flex; height:461px;}
.popup_content li {height:30px; text-align:left; font-size:15px; }
.popup_content li:first-child {font-weight:bold; font-size:16px; color:#000; margin-bottom:15px;}

.popup_content_left {display:flex; flex-wrap:wrap; width:650px; margin-right:30px;}
.popup_content_left ul {width:130px;}

.popup_season {padding-right:80px; border-right:1px solid #e2e2e2;}
.popup_season li:first-child {font-size:16px; font-weight:bold; color:#000; margin-bottom:15px;}

.popup_content_right {display:flex; margin-left:100px;}
.popup_content_right ul:first-child {margin-right:50px;}



/* 슬라이드  */
.slide_wrap {
  height: 887px;
}

.slide_wrapper {
  height:100%;
  scroll-behavior: smooth;
}

.slidebox {
  width: 581px;
  margin-right: 30px;
  height: 100%;
  border-radius: 40px;
  padding-bottom: 30px;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
  float:left;
}

.slidebox-txt {text-align:left; margin:15px 35px 0;}
.slidebox-txt > span {font-size:16px; font-weight:300;}
.slidebox-txt > p {font-size:30px; font-weight:550; margin-bottom:20px;}

.slide-tag {display:flex; flex-wrap:wrap;}
.slide-tag a {margin:0 7px 7px 0; background-color:#eaeaea; border:0; border-radius:10px; height:21px; padding:0 12px; font-size:12px; line-height:19px; color:#000; font-weight:550;}

/* 카테고리 */
.category_title {width:1350px; margin:0 auto; text-align:center; height:50px;}
.category_title > h2 {font-size:40px; font-weight:bold; color:#000;}
.category_title > h2 > a {color:#000;}

.cate {margin:50px 300px 110px; display:flex;}
.cate li {margin-left:22px;}
.cate li:first-child {margin-left:0;}
.cate img {width:100%;}

/* 배너 */
.banner {width:1700px; margin:170px auto 160px; display:flex;}

/* NEW + */
.newtitle {width:1350px; margin:200px auto 30px; text-align:center; height:50px;}
.newtitle > h2 {font-size:40px; font-weight:700; color:#000;}
.newtitle > h2 > a {color:#000;}

.newplus {width:1350px; margin:0 auto; display:flex; flex-wrap:wrap;}

.item {width:320px; margin:0 7px; padding-bottom:30px;}

.newbox {text-align:left;}

.newbox_img img {width:320px; height:320px; border-radius:15px;}
.newbox_img:first-child img {border:1px solid #f1f1f1;}

.newbox_txt {margin-top:15px;}
.newbox_txt > a {letter-spacing:0.5px; color:#000; font-size:13px; display:inline-block; width:100%; }

.reservation {font-size:13px; color:#007cd8; font-weight:550;}

.persent {font-size:15px; color:#8581d6; font-weight:bold; margin-right:3px;}

.saleprice {font-size:12px; color:#000; font-weight:bold; margin-right:3px;}

.price {text-decoration:line-through; font-size:11px; color:#999; padding-right:10px; font-weight:bold;}

.review {font-size:12px; color:#1f5180; font-weight:550;}

/* 베스트 아이템 */
.bestitem_title {width:1350px; margin:140px auto 40px;}
.bestitem_title > h2 {text-align:center; font-size:40px; font-weight:bold; color:#000;}
.bestitem_title > h2 > a {color:#000;}

/* BEST10 */
.best_item {width:250px; margin:0 7px; padding-bottom:30px; display:inline-block; height:auto;}

.bestbox {text-align:left; position:relative; }

.ranking {position:absolute; top:1px; left:1px; opacity:1; width:0px; height:0px; border-bottom:45px solid transparent; border-left:45px solid #000; color:#fff; z-index:1; border-radius:5px;}

.ranking .mos {color:#fff; font-size:11px; font-weight:bold; margin:7px 0 0 -35px; font-family: "Noto Sans KR";}

.bestbox_img img {width:250px; height:250px; border-radius:15px;}

.bestbox_txt {margin-top:15px;}
.bestbox_txt > a {letter-spacing:0.5px; color:#000; font-size:13px; display:inline-block; width:100%; }

/* Trend  ----트랜드     */

.trend_title {width:1350px; margin:180px auto 20px;}
.trend_title > h2 {text-align:center; font-size:40px; font-weight:bold; color:#000;}
.trend_title > h2 > a {color:#000;}

.trend_wrap {display:flex;}
.trend_wrap .trend_item_list:nth-child(2) {width:29.15%; flex-shrink:0;}
.trend_wrap .trend_item_list:nth-child(2) > a {display:inline-block; width:100%; position:relative;}
.trend_wrap .trend_item_list:nth-child(2) > a > img {width:100%;}

.trend_txt_box {position:absolute; display:flex; flex-direction:column; justify-content:center; align-items:center; width:100%; height:100%; top:0; left:0;}
.trend_txt_box > h3 {color:#fff; font-size:40px; font-weight:600;}
.trend_txt_box > p {color:#fff; font-size:16px; font-weight:500;}

.trend_item_list {width:100%; display:flex; flex-wrap:wrap;}

.trend_item {position:relative; width:50%;}
.trend_item:hover > .preview_in_box {opacity:1;}

.trend_item img {width:100%;}

.preview_in_box {opacity:0; background-color:rgba(0,0,0,0.5); position:absolute; top:0; left:0; height:100%; width:100%; text-align:center; transition:0.5s; display:flex; justify-content:center; align-items:center;}
.preview_in_box span {display:inline-block; margin:0 10px; border:1px solid #fff; border-radius:100%; width:40px; height:40px; line-height:36px; text-align:center;}
.preview_in_box span i {font-size:24px; line-height:20px; margin-top:10px;}

/* SPC PRICE */
.spc_slider {width:100%; margin:0 auto;}

.spc_wrap {background-color:#f1f1f1; padding:10px 0 50px; width:100%; margin:190px auto 0;}

.spc_title {width:1350px; margin:50px auto 0; height:50px;}
.spc_title > h2 {color:#000; text-align:center; font-size:40px; font-weight:bold;}

.spcbox {text-align:left; position:relative; width:100%; height:450px;}

.ranking_spc {position:absolute; top:0; left:0; opacity:1; width:0px; height:0px; border-bottom:90px solid transparent; border-left:90px solid #000; color:#fff; z-index:1; border-radius:10px;}

.ranking_spc .mos_spc {color:#8581d6; font-size:19px; font-weight:bold; margin:14px 0 0 -78px; font-family: "Noto Sans KR";}

.spcbox_img {width:100%;}

.spcbox_img img {width:100%; height:350px; border-radius:10px;}

.spcbox_txt {padding:20px;}
.spcbox_txt > a {letter-spacing:0.5px; color:#000; font-size:15px; display:inline-block; width:100%; overflow:hidden; height:21px; font-weight:bold; font-family: 'Noto Sans KR', sans-serif; text-overflow: ellipsis;}
.spcbox_txt span {font-size:15px;}

/* 시즌 + */
.season_wrap {width:1350px; margin:190px auto 150px;}

.season_title {width:1350px; margin:50px auto 30px; height:50px}
.season_title > h2 {font-size:40px; text-align:center; font-weight:bold;}
.season_title > h2 > a {color:#000;}

.season_ul {display:flex; flex-wrap:wrap;}

.season_item {width:260px; height:260px; margin:0 10px 10px 0; border-radius:15px; overflow:hidden; position:relative;}
.season_item img {width:100%; height:100%;}

.season_item:hover > .preview_in_box {opacity:1;}

/* Footer_banafit */
.footer_banafit {background-color:#f7f7f7; padding:30px 0;}
.footer_banafit > ul {display:flex; width:1350px; margin:0 auto; justify-content:space-between; align-items:center;}
.footer_banafit li {display:flex; align-items:center;}
.footer_banafit li h3 {font-size:22px; font-weight:600;}
.footer_banafit li p {font-size:14px; font-weight:400; margin:0; text-align:left; font-weight:600;}
.footer_banafit li span {display:inline-block; background-color:#fff; border:0; border-radius:100%; width:75px; height:75px; line-height:75px; text-align:center; margin-right:10px;}
.footer_banafit li span img {width: 50px; vertical-align: middle;}
.footer_banafit li > a {display:inline-block; width:150px; height:35px; border:1px solid #afafaf; border-radius:18px; line-height:34px; padding:0 15px; font-size:15px; color:#676767; text-align:left;}

/* Footer */
.footer_wrap {position:relative; background-color:#f3f3f4; font-size:13px; color:#767678;}

.foot {width:1350px; margin:0 auto; overflow:hidden; padding:80px 0;}

.foot_info1 {float:left;}
.foot_info1 > ul {overflow:hidden; margin-bottom:30px;}
.foot_info1 > ul li {float:left; margin-left:30px; font-weight:bold;}
.foot_info1 > ul li a {color:#222; font-size:15px;}
.foot_info1 > ul li:first-child {margin-left:0;}
.foot_info1 p {line-height:30px; font-size:15px; font-weight:300; text-align:left;}
.foot_info1 p:last-child {color:#000; font-weight:400;}

.foot_info2 {float:right;}
.foot_info2 > ul {float:left; margin-right:55px;}
.foot_info2 li {line-height:30px; font-weight:bold; text-align:right;}
.foot_info2 li a {color:#222; font-size:15px; font-weight:bold;}
.foot_info2 > div {float:left; text-align:right;}
.foot_info2 > div > p {font-size:47px; font-weight:bold; margin-bottom:12px; color:#222; margin-top:-12px;}
.foot_info2 > div span {display:block; line-height:27px; font-size:15px; font-weight:400;}



/* Detail */

.content {width:73%; margin:100px auto 0; height:934px;}

.content_img {width:42%; float:left;}

.info {width:50%; float:right; position:relative; height:934px;}
.detail_shipping {margin:-33px 0 14px; text-align:left;}
.icon_box {position:absolute; top:-34px; right:0; width:88px;}
.icon_box i {font-size:25px;}
.icon_box span {font-size:14px; font-weight:600; margin-right:25px;}

/* info_product */
.info_product {padding:30px 0 10px; border-bottom:1px solid #e2e2e2; border-top:2px solid #000; }
.info_product > h3 {color:#000; font-size:25px; font-weight:bold; line-height:33px; text-align:left;}

.product_price_box {display:block; margin:30px 0 15px; font-weight:bold; text-align:left;}

.detail_persent {font-size:24px; color:#ff4600; letter-spacing: -0.5px; margin-right:6px}

.detail_saleprice {font-size:24px; letter-spacing: -0.5px; color:#000;}

.detail_price {font-size:17px; text-decoration:line-through; color:#b7b7b7; margin-left:8px; letter-spacing: -0.5px;}

/* info_table */
.info_table {margin-top:30px;}

.info_table_top {width:100%; text-align:left;}
.info_table_top th {font-size:15px; padding:6px 0; vertical-align: top;}
.info_table_top tr:nth-child(4) {margin-bottom:10px;}
.info_table_top td {font-size:15px; font-weight:600; padding:6px 0;}

.detail_member {background-color:#f8f8f8; text-align:left; line-height:30px; color:#000; font-size:14px; margin-top:10px; position:relative; height:45px; font-weight:600; position:relative; }
.detail_member > i {position:absolute; top:19px; right:20px;}
.detail_member > p {line-height:44px; margin-left:20px; font-size:15px;}
.detail_member > p > span {color:#ff4600;}

.d_day_txt {display:inline; color:#1a498f; margin-right:15px; font-size:15px; margin-bottom:0;}
.d_day_sub_txt {color:#969696; font-size:14px; padding-top:7px; font-weight:500; margin-bottom:0;}

.detail_member_wrap {width:100%; border:1px solid #e6e6e6; background-color:#fff; padding:10px 20px;}
.detail_member_wrap > div {font-size: 12px; padding: 7px 0 12px; color: #696969; line-height:20px;}
.detail_member_wrap > table {width:100%; font-size:15px; font-weight:400;}

.bot_info {margin-top:30px; border-top:1px solid #e2e2e2; text-align:left;}
.bot_info > p {margin-bottom:5px; padding-top:4px; line-height:28px; margin-top:25px; color:#000; font-size:15px; font-weight:bold;}
.bot_info > select {width:100%; height:45px; border:1px solid #e2e2e2; padding:0 50px 0 20px; font-size:15px;}
.bot_info > select option {font-weight:600;}

.total_price {margin-top:30px; border-top:2px solid #000; padding:30px 0; text-align:right;}
.total_price > span {margin-right:10px; font-weight:600;}
.total_price > strong {color:#ff4600; font-size:25px; font-weight:bold;}

.detail_button {display:flex;}
.detail_button button {width:50%; height:60px;}
.detail_button a {width:50%;}
.detail_button a > button {width:100%; height:60px;}


/* Detail_main */
.nav-tabs {width:1350px; margin:0 auto; }

.nav-item {width:33.3%;}

/* detail_pay */
.detail_pay {margin-top:15px;}

/* detail_상품상세 */
.delivery_box {margin-top:10px;}

.detail_img_box {margin:50px auto 0; width:100%; overflow:hidden; text-align:center; max-width:880px; }
.detail_img_box img {margin:0 auto; max-width:100%;}

/* detail_리뷰 */
.detail_review_img img {margin-top:80px;}

/* detail_배송 */
.detail_shipping_img img:first-child {margin-top:80px;}


/* About */
.about_wrap {margin-top:100px;}
.about_wrap li {background-color:#f1f1f1;}
.about_wrap li:first-child {background: linear-gradient( to bottom, transparent 44px, #000 10px );}

.about_title {width:100%; height:auto; text-align:center; margin-bottom:35px;}
.about_title {display:inline-block; width:100%; height:35px; line-height:35px; color:#383838; font-size:25px;}